export default {
	data() {
		return {
			groupList: [],
		}
	},
	onLoad: function() {
		uni.request({
			url: 'http://'+getApp().globalData.urlStr+'/GetAllUsersGroup',
			method: 'GET',
			data: {},
			success: res => {
				if (res.data.code == 0) {
					this.groupList = res.data.data;
				}
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		selectGroup(e) {
			getApp().globalData.groupId = e
			uni.navigateTo({
				url: "../user/userList"
			})
		}
	}
}
